.about-card {
  -webkit-animation: text-flicker-in-glow 1s linear both;
  animation: text-flicker-in-glow 1s linear both;
}

.about {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 10px;
}
.about-card {
  width: 50%;
  padding-left: 20px;
  padding-right: 20px;
  text-align: center;
  border-radius: 12px;
  background-color: #e3f3c4;
}
